// Services
.services {
  position: relative;
  z-index: 2;
  padding-top: 6px;
  padding-bottom: 120px;
  background: $fontColor;
  color: $colorWhite;
  &__nav {
    position: relative;
    // width: 100%
    display: flex;
    align-items: center;
    margin: 47px 0 0;
    padding: 0 0 20px;
    overflow-x: auto;
    li {
      flex: 0 0 auto;
      display: inline-block;
      margin-right: 10px;
      &:last-child {
        margin-right: 0; } }
    a {
      position: relative;
      display: inline-block;
      padding: 5px;
      font-family: $latoFont;
      font-weight: bold;
      font-size: 14px;
      line-height: 17px;
      letter-spacing: 0.06em;
      text-transform: uppercase;
      padding: 6px 19.5px 6px 20px;
      border: 1px solid #3E3E3E;
      transition: color .2s $ease, background .2s $ease, border .2s $ease;
      &:hover {
        background: rgba(196, 196, 196, 0.2); } }
    li {
      &.is-active {
        a {
          color: $fontColor;
          border-color: $colorPrimary;
          background: linear-gradient(180deg, #F5A02E 0%, #ED8900 100%); } } } }

  &__tab {
    margin-top: 39px; }

  &__image {
    font-size: 0;
    margin-right: -15px !important;
    clip-path: polygon(0 0, 100% 0%, 94% 100%, 0% 100%);
    img, picture {
      max-width: 100%; } }

  &__text {
    margin: auto 0 auto auto;
    max-width: 380px;
    padding-right: 8px;
    padding-top: 17px;
    .h3-title {
      position: relative;
      padding-bottom: 14px;
      margin-bottom: 34px;
      &::after {
        display: inline-block;
        content: ' ';
        position: absolute;
        bottom: 0;
        left: 0;
        height: 1px;
        width: 140px;
        background: $colorPrimary; } }
    .btn {
      margin-top: 63px; } } }

@include r($hdplus) {
  .services {
    // &__nav
    //   a
    //     font-weight: 400
    &__image {
      margin-right: -40px !important;
      .scaler {
        padding-bottom: 0 !important;
        height: 623.81px !important; } }
    // &__nav
    //   li
    //     margin-right: 7px
 } }    //     padding-left: 2px

@include r($hd) {
  .services {
    // &__nav
    //   a
    //     font-weight: 700
    &__image {
      margin-right: 10px !important;
      .scaler {
        height: 359px !important; } }
    &__text {
      padding-right: 0;
      margin-top: 0;
      padding-top: 2px;
      padding-left: 8px; } } }

@include r($xl) {
  .services {
    &__image {
      margin-top: auto;
      margin-bottom: auto;
      transform: translateY(5px);
      .scaler {
        height: 309px !important; } } } }

@include r($lg) {
  .services {
    padding-bottom: 100px;
    &__image {
      margin-right: 0px !important;
      .scaler {
        height: 410px !important; } }
    &__tab {
      margin-top: 34px; }

    &__text {
      max-width: 100%;
      margin-left: 0;
      padding-left: 0;
      padding-top: 20px;
      .btn {
        margin-top: 33px; } } } }

@include r($md) {
  .services {
    padding-bottom: 0;
    padding-top: 0px;
    margin-top: -9px;
    &__nav {
      margin-top: 28px; }
    &__tab {
      margin-top: 14px; }
    &__image {
      .scaler {
        height: 350px !important; } } } }

@include r($sm) {
  .services {
    padding-top: 39px;
    &__nav {
      margin-top: 41px; }
    &__text {
      padding-top: 8px; }
    &__image {
      .scaler {
        height: 170px !important; } } } }
