
// Portfolio project
.portfolio-project {
  &__slider {
    position: relative;
    z-index: 1; }

  &__nav {
    position: absolute;
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 2;
    bottom: 0;
    left: 0;
    right: 0;
    background: rgba(15, 15, 15, 0.75);
    padding: 30px 30px; }

  &__nav-wrapper {
    display: flex;
    align-items: center;
    justify-content: center;
    .swiper-wrapper {
      width: 620px; }

    .swiper-slide {
      cursor: pointer;
      width: 60px;
      .hexagon {
        &__wrapper {
          width: 60px;
          height: 68px;
          background: $fontColor;
          transition: background .25s $ease; }
        &__image {
          margin: 1px;
          img {
            filter: grayscale(100%); }
          &::after {
            display: inline-block;
            content: ' ';
            position: absolute 0;
            z-index: 2;
            background: rgba($colorPrimary, 0.2);
            opacity: 0;
            backface-visibility: hidden;
            transition: opacity .25s $ease; } } }

      &.is-active {
        .hexagon {
          &__wrapper {
            background: $colorPrimary; }
          &__image {
            &::after {
              opacity: 1; } } } }
      &:hover {
        .hexagon {
          &__image {
            &::after {
              opacity: 1; } } } } }

    .swiper-nav-arrow--prev {
      margin-right: 20px; }
    .swiper-nav-arrow--next {
      margin-left: 20px; } }

  &__download {
    position: absolute;
    right: 60px;
    color: $colorPrimary;
    font-size: 0;
    cursor: pointer;
    .ico {
      font-size: 40px; }
    &--mobile {
      display: none; } }

  &__info {
    margin-top: 40px; } }


// .project-info
.project-info {
  &__title {
    font-family: $baseFont; }
  &__partner {
    margin-top: 15px;
    font-family: $robotoFont;
    font-size: 18px;
    line-height: 152.34%;
    color: $colorWhite;
    span {
      font-size: 22px;
      color: $colorPrimary;
      &.separator {
        color: $colorWhite;
        padding-left: 2px;
        padding-right: 2px; } } }
  &__description {
    margin-top: 27px;
    max-width: 890px; }
  &__description-more {
    display: none;
    font-family: $robotoFont;
    font-weight: normal;
    font-size: 16px;
    line-height: 24px;
    color: rgba($colorPrimary, .7);
    cursor: pointer;
    margin-top: 13px;
    &:hover {
      color: $colorPrimary; } }
  &__platform {
    margin-top: 26px;
    font-family: $robotoFont;
    font-size: 18px;
    line-height: 152.34%;
    letter-spacing: 0.04em; }


  &__filters {
    position: relative;
    width: 100%;
    max-width: 249px;
    margin-top: 20px;
    margin-left: auto;
    &::before {
      display: inline-block;
      content: ' ';
      position: absolute;
      left: 0;
      top: 0;
      bottom: calc(60px + 41px);
      width: 1px;
      background: $colorPrimary; } } }


.project-filters {
  &__section {
    margin-top: -5px;
    padding-left: 60px;
    &--lg {
      display: none; }
    & + & {
      margin-top: 32px; }

    .ui-radio + .ui-radio {
      margin-top: 7px; } }

  &__section-title {
    margin-bottom: 12px;
    font-family: $robotoFont;
    font-weight: bold;
    font-size: 22px;
    line-height: 28px;
    letter-spacing: 0.04em;
    text-transform: uppercase; }

  &__section-more {
    margin-top: 17px;
    a {
      font-family: $latoFont;
      font-weight: bold;
      font-size: 12px;
      line-height: 14px;
      letter-spacing: 0.06em;
      text-transform: uppercase;
      color: #3E3E3E;
      padding: 6px 20px;
      border: 1px solid #3E3E3E; } }
  &__cta {
    margin-top: 61px; } }


@include r($hdplus) {
  .portfolio-project {
    &__info {
      margin-top: 38px; }
    &__download {
      right: 70px; } } }

@include r($hd) {
  .portfolio-project {
    &__nav {
      padding: 16px 20px 20px; }
    &__download {
      right: 60px; } }

  .project-info {
    &__description {
      max-width: 570px; }
    &__platform {
      margin-top: 18px; } } }

@include r($xl) {
  .portfolio-project {
    &__nav {
      padding: 8px 20px 10px; }
    &__download {
      right: 40px; }
    &__info {
      margin-top: 40px;
      padding-left: 2px; } } }

@include r($lg) {
  .portfolio-project {
    &__nav {
      padding-bottom: 0; }
    &__nav-wrapper {
      .swiper-wrapper {
        width: 300px; } }
    &__info {
      margin-top: 108px; } }

  .project-info {
    &__description {
      max-width: 100%; }
    &__filters {
      max-width: 100%;
      margin-top: 32px;
      padding-top: 40px;
      &::before {
        bottom: auto;
        right: 0;
        height: 1px;
        width: 100%; } } }

  .project-filters {
    display: flex;
    flex-wrap: wrap;
    padding-top: 42px;
    &__section {
      flex: 0 0 percentage(5 / 16);
      margin-top: 0;
      padding-left: 0;
      margin-left: -2px;
      & + & {
        margin-top: 0; }
      &--lg {
        display: block;
        margin-left: 2px;
        .project-filters__section-title {
          opacity: 0; } } }
    &__section-more {
      margin-top: 24px; }
    &__cta {
      position: relative;
      margin-top: 58px;
      flex: 0 0 calc(100% + 80px);
      max-width: calc(100% + 80px);
      display: flex;
      justify-content: center;
      margin-left: -40px;
      margin-right: -40px;

      &::before, &::after {
        display: inline-block;
        content: ' ';
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        height: 1px;
        width: calc(50% - ((200px + 120px) / 2)); }
      &::before {
        left: 0;
        background: linear-gradient(to right, rgba($colorPrimary, 0) 0%, $colorPrimary 100%); }
      &::after {
        right: 0;
        background: linear-gradient(to left, rgba($colorPrimary, 0) 0%, $colorPrimary 100%); } } } }

@include r($md) {
  .portfolio-project {
    &__slider {
      padding-left: 15px;
      padding-right: 15px; }
    &__nav {
      display: none; }
    &__info {
      position: relative;
      margin-top: 23px; }
    &__download {
      top: 8px;
      right: 30px;
      &--mobile {
        display: inline-block; } } }

  .project-info {
    &__title {
      font-family: $robotoFont;
      font-weight: 500;
      font-size: 24px;
      line-height: 37px; }
    &__partner {
      margin-top: 4px;
      font-size: 15px;
      margin-left: -4px;
      span {
        font-size: 15px; }
      span.separator {
        padding-right: 0; } }
    &__description {
      margin-top: 12px;
      overflow-wrap: break-word;
      word-wrap: break-word;
      word-break: normal;
      line-break: auto;
      hyphens: none;
      p {
        font-size: 15px; } }
    &__description-wrapper {
      height: 88px;
      overflow: hidden;
      transition: height .25s $ease;
      &.is-visible {
        // height: auto
 } }        // height to be set with js
    &__description-more {
      display: inline-block; }

    &__filters {
      margin-top: 8px;
      padding-top: 0;
      &::before {
        display: none; } } }

  .project-filters {
    &__section {
      flex: 0 0 percentage(8 / 16);
      &--lg {
        display: none; }
      & + & {
        margin-left: -8px; } }
    &__section-more {
      display: inline-block !important; }
    &__cta {
      display: none; } } }

@include r($sm) {
  .portfolio-project {
    &__slider {
      padding-left: 0px;
      padding-right: 0px; }
    &__info {
      margin-top: 19px;
      padding-left: 0; }
    &__download {
      top: 8px;
      right: 20px; } }

  .project-info {
    &__partner {
      margin-left: -2px; }
    &__description-wrapper {
      height: 180px;
      p {
        line-height: 1.55; } } }

  .project-filters {
    &__section {
      flex: 0 0 100%;
      margin-left: 0;
      & + & {
        margin-top: 32px;
        margin-left: 0px; } }
    &__section-more {
      margin-top: 22px; } } }
