/* HEADER*/
.header {
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  z-index: 9;
  background: linear-gradient(180deg, rgba(15, 15, 15, 0.8) 0%, rgba(15, 15, 15, 0.2) 50.52%, rgba(15, 15, 15, 0) 100%);
  color: $colorWhite;
  will-change: transform;
  backface-visibility: hidden;
  transform-style: preserve-3d;
  &__wrapper {
    display: flex;
    align-items: center;
    padding: 19px 60px 18px; }

  &__logo {
    flex: 0 0 auto;
    padding-right: 15px;
    font-size: 0;
    .ico {
      font-size: 43px; } }

  &__menu {
    flex: 0 1 auto;
    display: flex;
    align-items: center;
    margin: 0 0 0 auto;
    padding: 0 0 4px;
    li {
      display: inline-block;
      margin-right: 30px;
      font-family: $latoFont;
      font-weight: bold;
      font-size: 14px;
      line-height: 17px;
      letter-spacing: 0.06em;
      text-transform: uppercase;
      &:last-child {
        margin-right: 0; } }
    a {
      position: relative;
      display: inline-block;
      padding: 8px 10px;
      transition: color .25s ease;
      &::after {
        display: inline-block;
        content: ' ';
        position: absolute;
        bottom: 0;
        left: 50%;
        width: 0;
        height: 1px;
        background: $colorPrimary;
        transform: translateX(-50%);
        transition: width .25s ease; }
      &:hover {
        &::after {
          width: calc(100% - 40px); } } }
    li.is-active a::after {
      width: calc(100% - 40px); } }

  &__cta {
    margin-left: 40px;
    padding-bottom: 2px; }

  &__hamburger {
    display: none;
    margin-left: auto;
    padding: 10px;
    margin-right: -12px;
    cursor: pointer; }



  &.is-fixed {
    // should be normalized to -100% scroll at this point
    position: fixed;
    transform: translate3d(0,-100%,0) !important;
    transition: transform .25s ease;
    .header__wrapper {
      padding-top: 10px;
      padding-bottom: 10px; } }

  &.is-fixed-visible {
    transform: translate3d(0,0,0) !important; }

  &.is-transitioning {
    transition: transform .25s ease;
    transform: translate3d(0,-100%,0) !important; }

  &[data-modifier="dark"] {
    background: $fontColor; } }

