@include r($xl) {
  .header {
    &__wrapper {
      padding-left: 40px;
      padding-right: 40px; } } }

@include r($lg) {
  .header {
    &__menu {
      display: none; }
    &__cta {
      display: none; }
    &__hamburger {
      display: block; } } }

@include r($md) {
  .header {
    background: $fontColor;
    &__wrapper {
      padding: 10px 20px;
      max-height: 50px; }
    &__hamburger {
      transform: translateY(1.5px); }
    &__logo {
      .ico {
        font-size: 30px; } } } }
