
// Related
.portfolio-related {
  margin-top: 95px;
  &__title {
 }    // h3-title

  &__grid {
    margin-top: 33px; } }

// .portfolio-related-card
.portfolio-related-card {
  position: relative;
  z-index: 1;
  overflow: hidden;
  box-shadow: 0px 6px 12px rgba(0, 0, 0, 0.6);
  &__image {
    position: relative;
    .scaler {
      padding-bottom: percentage(160 / 300); } }
  &__info {
    position: absolute 0;
    z-index: 2;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    padding: 14px 20px;
    background: rgba($fontColor, .6);
    opacity: 0;
    pointer-events: none;
    will-change: opacity;
    transition: opacity .1s $ease; }
  &__title {
    font-weight: bold;
    font-size: 22px;
    line-height: 28px;
    text-transform: uppercase;
    will-change: transform;
    transform: translate3d(0,100%,0);
    transition: transform .25s $ease; }
  &:hover {
    .portfolio-related-card {
      &__info {
        opacity: 1;
        pointer-events: all; }
      &__title {
        transform: translate3d(0,0,0); } } } }

@include r($hd) {
  .portfolio-related {
    &__grid {
      .col {
        flex-basis: percentage(1 / 3);
        max-width: percentage(1 / 3);
        &:nth-child(4) {
          display: none; } } } } }

