
// pagination
.swiper-pagination {
  display: flex;
  justify-content: center; }

.swiper-pagination-bullet {
  display: inline-block;
  padding: 10px;
  .hexagon {
    transition: filter .2s $ease;
    &__wrapper {
      width: 9px;
      height: 10px;
      background: $colorPrimary; }
    &__image {
      margin: 1px;
      background: $fontColor;
      transition: background .2s $ease; } } }

.swiper-pagination-bullet-active {
  .hexagon {
    filter: drop-shadow(0px 6px 12px rgba(black, 0.6));
    &__image {
      background: $colorPrimary; } } }


// navigation
.swiper-nav-full {
  position: absolute;
  z-index: 2;
  top: 0;
  bottom: 0;
  width: 100px;
  background: linear-gradient(0deg, rgba(15, 15, 15, 0.75), rgba(15, 15, 15, 0.75));
  color: $colorWhite;
  opacity: 0;
  cursor: pointer;
  will-change: opacity;
  transition: opacity .25s $ease;
  .ico {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    font-size: 12px; }
  &:hover {
    opacity: 1; }
  &--next {
    right: 0; }
  &--prev {
    left: 0;
    .ico {
      transform: translate(-50%,-50%) rotate(180deg); } } }

@include r($md) {
  .swiper-nav-full {
    width: 50px;
    opacity: 1;
    background: transparent;
    color: $colorPrimary; } }

// hexagon arrows
.swiper-nav-arrows {
  display: flex;
  align-items: flex-end;
  &__label {
    font-family: $robotoFont;
    font-weight: bold;
    font-size: 14px;
    line-height: 130%;
    text-align: right;
    letter-spacing: 0.04em;
    text-transform: uppercase;
    padding-right: 20px; }
  &__clickable {
    display: flex;
    align-items: center; } }

.swiper-nav-arrow {
  cursor: pointer;
  & + & {
    margin-left: 20px; }
  .hexagon {
    &__wrapper {
      width: 34px;
      height: 40px;
      transition: background .25s $ease; }
    &__image {
      color: $fontColor;
      align-items: center;
      justify-content: center;
      font-size: 0;
      .ico {
        font-size: 12px; } } }
  &:first-child, &--prev {
    .hexagon__image {
      .ico {
        transform: rotate(180deg) translateX(2px); } } }
  &:last-child, &--next {
    .hexagon__image {
      .ico {
        transform: translateX(2px); } } }
  &:hover {
    .hexagon {
      &__wrapper {
        background: #EE6500; } } } }
