//backstage
.backstage {
  position: relative;
  z-index: 2;
  // padding-top: 80px
  padding-bottom: 100px;
  // background: $fontColor
  color: $colorWhite;

  &__masonry {
    margin: 48px -10px -10px;
    .swiper-wrapper {}

    .grid-sizer {
      width: percentage(1 / 7); } }

  &__item {
    width: percentage(1 / 7);
    font-size: 0;
    padding: 10px;
    &--wide {
      width: percentage(2 / 7); } }
  &__image {
    position: relative;
    overflow: hidden;
    cursor: pointer;
    &.is-dynamic {
      opacity: 0;
      will-change: opacity;
      transition: opacity .1s $ease;
      &.is-loaded {
        opacity: 1; } }
    img, picture {
      width: 100%;
      // TODO - bug exporting image
      transform: scale(1.02); }
    &::before, &::after {
      display: inline-block;
      content: ' ';
      position: absolute 0;
      opacity: 0;
      will-change: opacity;
      backface-visibility: hidden;
      pointer-events: none; }
    &::before {
      background: $fontColor;
      z-index: 2;
      transition: opacity .3s ease-out; }
    &::after {
      z-index: 3;
      background: transparent svg-load('../icons/svg-mono/search.svg') no-repeat center center;
      background-size: 26.5px 26.5px;
      transform: scale(1.5);
      transition: opacity .2s .1s $ease, transform .2s .1s $ease; }

    &:hover {
      &::before {
        opacity: 0.7; }
      &::after {
        opacity: 1;
        transform: scale(1); } } }

  &__more {
    margin-top: 30px;
    display: flex;
    justify-content: flex-end; } }

@include r($hd) {
  .backstage {
    &__masonry {
      // margin: 48px -10px -10px
      .grid-sizer {
        width: percentage(1 / 5); } }

    &__item {
      width: percentage(1 / 5);
      &--wide {
        width: percentage(2 / 5); } } } }

@include r($xl) {
  .backstage {
    &__masonry {
      // slider !
      margin-left: -40px;
      margin-right: -40px;
      .grid-sizer {
        width: 192px; }
      .swiper-slide {
        width: calc((192px * 7) + 20px); } }
    &__item {
      width: 192px;
      display: block !important;
      &--wide {
        width: calc((192px * 2) + 0px); } }

    &__more {
      display: none; } } }

@include r($lg) {
  .backstage {
    &__masonry {
      .grid-sizer {
        width: 177px; }
      .swiper-slide {
        width: calc((177px * 7) + 20px); } }
    &__item {
      width: 177px;
      &--wide {
        width: calc((177px * 2) + 0px); } } } }


@include r($md) {
  .backstage {
    padding-top: 46px;
    padding-bottom: 66px;
    &__title {
      font-family: $baseFont;
      font-weight: bold;
      font-size: 24px;
      line-height: 31px; }
    &__masonry {
      // slider !
      margin-top: 16px;
      margin-left: -20px;
      margin-right: -20px;
      .swiper-slide {
        max-height: 282px !important; }
      .grid-sizer {
        width: 162px; }
      .swiper-slide {
        width: calc((162px * 7) + 10px); } }
    &__item {
      width: 162px;
      padding: 5px;
      &--wide {
        width: 162px; } } } }

@include r($sm) {
  .backstage {
    padding-top: 36px;
    padding-bottom: 78px;
    &__masonry {
      margin-top: 8px; } } }
