// buttons
button {
  -webkit-appearance: none;
  border: 0;
  box-shadow: none;
  cursor: pointer;
  &:focus {
    outline: none; } }

.btn {
  display: inline-block;
  position: relative;
  text-align: center;
  cursor: pointer;
  box-shadow: none;
  font-family: $latoFont;
  font-weight: bold;
  font-size: 14px;
  line-height: 17px;
  letter-spacing: 0.06em;
  text-transform: uppercase;
  transition: all .25s ease;
  &.disabled, &[disabled] {
    background: #E5E5E5;
    color: $colorWhite;
    cursor: not-allowed;
    pointer-events: none; }
  &:focus, &:active {
    outline: none; }
  &--block {
    display: block;
    width: 100%; }

  &-primary {
    background: linear-gradient(180deg, #F5A02E 0%, #ED8900 100%);
    color: $fontColor;
    padding: 12px 40px;
    border-radius: 0px;
    &:hover {
      background: linear-gradient(180deg, #ED8900 0%, #F5A02E 100%); } }

  &-outline {
    background: transparent;
    border: 1px solid $colorPrimary;
    color: $colorPrimary;
    padding: 11px 39px;
    border-radius: 0px;
    &:hover {
      background: $colorPrimary;
      color: white; } } }

