// About
.about {
  position: relative;
  z-index: 2;
  padding-top: 6px;
  padding-bottom: 120px;
  background: $fontColor;
  color: $colorWhite;
  overflow: hidden;
  &__content {
    position: relative;
    z-index: 2; }
  &__head {}

  &__wrapper {
    margin-top: 46px; }

  &__text {
    margin-top: 36px;
    max-width: 310px; }
  &__text-more {
    display: none;
    font-family: $robotoFont;
    font-weight: normal;
    font-size: 16px;
    line-height: 24px;
    color: rgba($colorPrimary, .7);
    cursor: pointer;
    margin-top: 13px;
    &:hover {
      color: $colorPrimary; } }


  &__cta {
    margin-top: 88px; }

  &__slider {
    margin-top: -20px;
    padding-left: 30px;
    .swiper-nav-arrows {
      margin-top: 28px;
      justify-content: flex-end; } }
  &__slide {
    position: relative;
    padding-top: 20px;
    padding-bottom: 20px;
    height: calc(452px + 40px);
    width: calc((100% / 5) - 55px);
    .hexagon {
      position: absolute;
      left: 50%;
      top: 20px;
      transform: translateX(-50%);
      filter: drop-shadow(0px 6px 12px rgba(black, 0.6));
      &__wrapper {
        width: 390px;
        height: 452px;
        background: $fontColor;
        transition: background .1s .2s $ease; }
      &__image {
        margin: 2px;
        opacity: 0.5;
        transition: opacity .2s $ease; } }
    &.swiper-slide-active {
      z-index: 5;
      + .swiper-slide {
        z-index: 4;
        + .swiper-slide {
          z-index: 3;
          + .swiper-slide {
            z-index: 2; } } }
      .hexagon {
        &__wrapper {
          background: $colorPrimary; }
        &__image {
          opacity: 1; } } }

    &.is-hidden {
      opacity: 0; } } }


@include r($hd) {
  .about {
    &__text {
      margin-right: -10px; }
    &__slider {
      margin-right: -10px;
      .swiper-nav-arrows {
        margin-right: 80px; } }
    &__slide {
      width: calc((100% / 5) - 64px); } } }

@include r($xl) {
  .about {
    &__wrapper {
      .row {
        align-items: center; } }
    &__slider {
      margin-top: 0px;
      margin-right: -2px;
      margin-left: -12px;
      padding-left: 0;
      .swiper-nav-arrows {
        display: none; } }
    &__slide {
      width: calc((100% / 5) - 68px); } } }

@include r($lg) {
  .about {
    padding-bottom: 40px;
    &__wrapper {
      .row {
        align-items: stretch; } }
    &__wrapper {
      margin-top: 6px; }
    &__text {
      margin-right: 0px;
      max-width: 100%; }
    &__cta {
      margin-top: 53px; }
    &__slider {
      margin-right: -16px; } } }

@include r($md) {
  .about {
    padding-bottom: 0;
    padding-top: 10px;
    &__background {
      display: none; }
    &__text {
      margin-top: 4px;
      p {
        font-size: 15px;
        line-height: 23px; } }
    &__text-wrapper {
      height: 95px;
      overflow: hidden;
      transition: height .25s $ease;
      &.is-visible {
        // height: auto
 } }        // height to be set with js
    &__text-more {
      display: inline-block; }

    &__cta {
      margin-top: 30px; }

    &__slider {
      margin-top: -10px;
      margin-right: -12px; }
    &__slide {
      height: calc(280px + 40px);
      width: calc((100% / 5) - 34px);
      .hexagon {
        &__wrapper {
          width: 240px;
          height: 280px; } } } } }

@include r($sm) {
  .about {
    padding-top: 10px;

    &__text {
      margin-top: 15px; }
    &__text-wrapper {
      height: 88px; }
    &__slider {
      margin-top: 0;
      margin-left: -20px;
      margin-right: -20px; }
    &__slide {
      width: calc((100% / 3) - 37px); }
    &__cta {
      margin-top: 23px; } } }
