
.gclose {
  background: transparent !important;
  top: -35px !important;
  right: -35px !important;
  opacity: 1 !important;
  padding: 0; }

.goverlay {
  background: rgba($fontColor, .8); }

.glightbox-container {
  display: flex;
  flex-direction: column;
  .gcontainer {
    // height: calc(100% - 120px)
    height: auto;
    margin: auto;
    overflow: visible;
    max-height: calc(100vh - 120px); } }

.gcontainer {
  max-width: 1720px;
  // margin: 60px auto
  background: $fontColor;
  transition: background .25s $ease;

  .gprev, .gnext {
    position: absolute;
    z-index: 2;
    top: 0;
    bottom: 0;
    width: 100px;
    height: 100%;
    background: black;
    // background: linear-gradient(0deg, rgba(15, 15, 15, 0.75), rgba(15, 15, 15, 0.75))
    color: $colorWhite;
    opacity: 0;
    cursor: pointer;
    will-change: opacity;
    transition: opacity .25s $ease, background .25s $ease;
    .ico {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%,-50%);
      font-size: 12px;
      width: 12px; }
    &:hover {
      opacity: 1; } }

  .gprev {
    left: 0;
    .ico {
      transform: translate(-50%,-50%) rotate(180deg); } }

  .gnext {
    right: 0; }

  .gclose {
    // background: linear-gradient(0deg, rgba(15, 15, 15, 0.75), rgba(15, 15, 15, 0.75))
    .ico {
      width: 20px;
      color: $colorWhite;
      font-size: 20px;
      transition: color .25s $ease; }
    &:hover {
      .ico {
        color: $colorPrimary; } } } }

.glightbox-closing {
  .gcontainer {
    background: transparent; } }

@include r($hdplus) {
  .gcontainer {
    max-width: 1310px; } }
@include r($hd) {
  .gcontainer {
    max-width: 1000px; } }
@include r($xl) {
  .gcontainer {
    max-width: 824px; } }
@include r($lg) {
  .gcontainer {
    max-width: 668px; } }
@include r($md) {
  .gcontainer {
    max-width: 100%;
    margin: 0;
    .gprev, .gnext {
      display: none; } }
  .gclose {
    top: 10px !important;
    right: 10px !important; }

  .glightbox-container {
    .gcontainer {
      height: 100%;
      max-height: 100%; } } }
