// Posters
.posters {
  position: relative;
  z-index: 2;
  padding-top: 6px;
  padding-bottom: 120px;
  background: $fontColor;
  color: $colorWhite;
  &__content {
    position: relative;
    z-index: 2; }
  &__description {
    margin-top: 82px;
    max-width: 860px; }

  &__wrapper {
    margin-top: 54px; }

  &__info {
    flex: 1 0 auto;
    position: relative; }
  &__info-card {
    position: absolute;
    left: 0;
    top: 50%;
    transform: translateY(-50%);
    padding: 7px 0 20px 2px;
    opacity: 0;
    transform: translate3d(0,calc(-50% - 100px),0);
    transition: opacity .25s $ease, transform .25s $ease;
    p {
      color: $colorBackground; }
    .h2-title {
      margin-top: 6px;
      margin-bottom: 16px; }
    .btn {
      margin-top: 35px; }
    &.is-active {
      opacity: 1;
      transform: translate3d(0,-50%,0); } }


  &__slider {
    padding-left: 50px;
    margin-right: calc(((100vw - 1260px) / 2) * -1);
    .swiper-wrapper {
      min-height: 500px;

      align-items: center; }
    .swiper-slide {
      width: 166px;
      transition: width .2s $ease;
      &.swiper-slide-active {
        width: 354px; } }
    .swiper-nav-arrows {
      position: absolute;
      left: 374px;
      bottom: 0px;
      z-index: 3; } } }


.poster-card {
  position: relative;
  &__dev {
    position: absolute;
    top: 0;
    right: 0;
    background: white;
    color: $fontColor;
    font-size: 18px;
    font-weight: 700;
    z-index: 2; }
  &__image {
    position: relative;
    font-size: 0;
    overflow: hidden;
    img, picture {
      // TODO - bad export
      transform: scale(1.02); } } }


@include r($hd) {
  .posters {
    &__slider {
      padding-left: 20px;
      margin-right: calc(((100vw - 1120px) / 2) * -1); } } }

@include r(1380) {
  .posters {
    &__slider {
      margin-right: -130px; } } }
