// UTILS (desktop first)

// CUSTOM MARGIN UTILS
@for $i from 0 through 10 {
  .mt-#{$i} {
    margin-top: #{$i}em !important; }
  .mb-#{$i} {
    margin-bottom: #{$i}em !important; } }
@for $i from 0 through 10 {
  @include r($xl) {
    .mt-xl-#{$i} {
      margin-top: #{$i}em !important; }
    .mb-xl-#{$i} {
      margin-bottom: #{$i}em !important; } } }
@for $i from 0 through 10 {
  @include r($lg) {
    .mt-lg-#{$i} {
      margin-top: #{$i}em !important; }
    .mb-lg-#{$i} {
      margin-bottom: #{$i}em !important; } } }
@for $i from 0 through 10 {
  @include r($md) {
