// Partners
.partners {
  position: relative;
  z-index: 2;
  padding-top: 6px;
  padding-bottom: 60px;
  background: $fontColor;
  color: $colorWhite;
  &__grid {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    margin: 68px -10px -20px; }
  &__col {
    width: 100%;
    min-width: 1px;
    flex: 0 0 percentage(1 / 6);
    max-width: percentage(1 / 6);
    padding: 20px 10px;
    display: flex;
    justify-content: center;
    &:nth-child(6n + 1) {
      justify-content: flex-start; }
    &:nth-child(6n) {
      justify-content: flex-end; } }
  &__load-cta {
    position: relative;
    margin-top: 99px;
    display: flex;
    justify-content: center;
    &::before, &::after {
      display: inline-block;
      content: ' ';
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
      height: 1px;
      width: calc(50% - ((200px + 100px) / 2)); }
    &::before {
      left: 0;
      background: linear-gradient(to right, rgba($colorPrimary, 0) 0%, $colorPrimary 100%); }
    &::after {
      right: 0;
      background: linear-gradient(to left, rgba($colorPrimary, 0) 0%, $colorPrimary 100%); } } }


// partner-card
.partner-card {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 80px;
  max-width: 220px;
  width: 100%;
  padding: 0px 10px;
  font-size: 0;
  img, picture {
    opacity: 0.6;
    max-width: 100%;
    max-height: 100%;
    transition: opacity .25s $ease; }
  &__wrapper {
    flex: 1 0 auto;
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%; }
  &:hover {
    img, picture {
      opacity: 1; } } }


@include r($hd) {
  .partners {
    &__col {
      // TODO - prefer javascript based hider
      flex-basis: percentage(1 / 5);
      max-width: percentage(1 / 5);
      &:nth-child(6n) {
        display: none; } } } }

@include r($xl) {
  .partners {
    &__col {
      flex-basis: percentage(1 / 4);
      max-width: percentage(1 / 4);
      &:nth-child(4n) {
        display: none; } } } }

@include r($lg) {
  .partners {
    &__grid {
      margin-top: 101px; }
    &__col {
      flex-basis: percentage(1 / 3);
      max-width: percentage(1 / 3);
      &:nth-child(3n) {
        display: none; } } } }

@include r($md) {
  .partners {
    padding-bottom: 0;
    &__grid {
      margin-top: 10px;
      margin-bottom: -10px; }
    &__col {
      padding-top: 10px;
      padding-bottom: 10px;
      &:nth-child(3n) {
        display: flex; }
      &:nth-child(12) {
        display: none; } }
    &__load-cta {
      margin-top: 57px;
      justify-content: flex-start;
      &::before, &::after {
        display: none; } } }

  .partner-card {
    min-height: 73px;
    max-width: 200px;
    img, picture {
      max-width: 90%;
      max-height: 90%; } } }

@include r($sm) {
  .partners {
    padding-top: 116px;
    &__col {
      flex-basis: percentage(1 / 2);
      max-width: percentage(1 / 2);
      &:nth-child(12) {
        display: flex; } }
    &__load-cta {
      margin-top: 40px; } }
  .partner-card {
    min-height: 52px;
    max-width: 200px; } }

// corners
.partner-card, .partner-card__wrapper {
  &::before,
  &::after {
    content: "";
    position: absolute;
    width: 0px;
    height: 0px;
    border: 1px solid $colorPrimary;
    opacity: 0;
    transition: width .25s $ease, height .25s $ease, opacity .1s $ease; } }

.partner-card {
  &:before {
    left: 0;
    top: 0;
    border-bottom: none;
    border-right: none; }
  &::after {
    bottom: 0;
    right: 0;
    border-left: none;
    border-top: none; } }

.partner-card__wrapper {
  &:before {
    right: -10px;
    top: 0;
    border-bottom: none;
    border-left: none; }
  &::after {
    bottom: 0;
    left: -10px;
    border-top: none;
    border-right: none; } }

.partner-card {
  &:hover {
    &:before, &::after {
      width: 13px;
      height: 13px;
      opacity: 1; }
    .partner-card__wrapper {
      &:before, &::after {
        width: 13px;
        height: 13px;
        opacity: 1; } } } }
