.mobile-navi {
  display: none;
  position: fixed 0;
  z-index: 8;
  background: $fontColor;
  color: $colorWhite;
  // display: flex
  flex-direction: column;
  pointer-events: none;
  opacity: 0;
  transform: translate3d(0,-100%,0);
  transition: transform .3s $ease;
  &__wrapper {
    flex: 1 1 auto;
    display: flex;
    flex-direction: column;
    padding: 81px 25px 20px; }
  &__menu {
    margin: 0;
    padding: 28px 0;
    list-style: none;
    text-align: center;
    li {
      display: block;
      margin-bottom: 30px;
      &:last-child {
        margin-bottom: 0; } }
    a {
      position: relative;
      display: inline-block;
      font-family: $latoFont;
      font-weight: bold;
      font-size: 14px;
      line-height: 17px;
      padding: 8px 10px;
      letter-spacing: 0.06em;
      text-transform: uppercase;
      transition: .25s $ease;
      &::after {
        display: inline-block;
        content: ' ';
        position: absolute;
        bottom: 0;
        left: 50%;
        width: 0;
        height: 1px;
        background: $colorPrimary;
        transform: translateX(-50%);
        transition: width .25s ease; }
      &:hover {
        &::after {
          width: calc(100% - 40px); } } } }
  &__cta {
    margin-top: 3px;
    text-align: center; }


  &.is-active {
    opacity: 1;
    pointer-events: all;
    transform: translate3d(0,0,0); } }

@include r($lg) {
  .mobile-navi {
    display: flex; } }

@include r($md) {
  .mobile-navi {
    &__wrapper {
      padding-top: 51px; }
    &__cta {
      .btn {
        min-width: 175px; } } } }
