// shared elements
.helper-link {
  font-size: 16px;
  line-height: 16px;
  color: $colorPrimary;
  text-decoration: underline;
  transition: color .25s $ease;
  &:hover {
    color $fontColor {} } }

// tabs
.js-tab {
  display: none; }


.section-bg {
  position: absolute 0;
  z-index: 1;
  display: flex;
  flex-direction: column;
  pointer-events: none;
  picture, img {
    width: 100%;
    height: 100%;
    flex: 1 1 auto;
    object-fit: cover; }
  &--bottom {
    top: auto; }
  &--center {
    top: 50%;
    bottom: auto;
    transform: translateY(-50%); }
  &--team {
    top: 44.6%; } }

// section-head
.section-head {
  &__front {
    // TODO - is h1 instance ?
    position: relative;
    z-index: 2;
    font-weight: bold;
    font-size: 64px;
    line-height: (83 / 64);
    text-transform: uppercase;
    transform: translate(-1px, 6px); }
  &__back {
    position: relative;
    z-index: 1;
    font-family: $robotoFont;
    font-weight: bold;
    font-size: 160px;
    line-height: (187 / 160);
    text-transform: uppercase;
    color: rgba($colorBackground, .25);
    transform: translateX(-84px);
    margin-bottom: -74px;
    white-space: nowrap; } }

