@include r($hd) {
  .section-bg {}

  .section-head {
    margin-right: -60px;
    // &__front
    //   font-weight: 700
    // &__back
 } }    //   font-weight: 700

@include r($lg) {
  .section-head {
    margin-right: 0;
    &__front {
      margin-top: 5px;
      font-family: $robotoFont;
      font-size: 36px;
      transform: translate(-1px, -2px); } } }

@include r($md) {
  .section-head {
    padding-top: 30px;
    &__back {
      font-family: $baseFont;
      font-weight: bold;
      font-size: 64px;
      line-height: 83px;
      transform: translateX(-21px);
      margin-bottom: -39px; }
    &__front {
      transform: translate(-1px, -4px); } } }

@include r($sm) {
  .section-head {
    padding-top: 0;
    margin-top: -10px;
    margin-right: -10px;
    &__back {
      transform: translate(-21px, 2px); }
    &__front {
      line-height: (44 / 36); } } }

.hexagon {
  &__wrapper {
    // size to be decralert in higher component
    position: relative;
    clip-path: polygon(0 25%, 50% 0, 100% 25%, 100% 75%, 50% 100%, 0 75%);
    background: $colorPrimary;
    display: flex;
    flex-direction: column; }
  &__image {
    // margin: 2px
    flex: 1 1 auto;
    display: flex;
    flex-direction: column;
    clip-path: polygon(0 25%, 50% 0, 100% 25%, 100% 75%, 50% 100%, 0 75%);
    img, picture {
      flex: 1 1 auto;
      width: 100%;
      height: 100%;
      object-fit: cover;
      object-position: center;
      transform: scale(1.02); } } }



