//.career-intro
.career-intro {
  padding-top: 146px;
  margin-bottom: 66px;
  color: #242020;
  &__description {
    margin-top: 17px;
    p {
      font-family: $robotoFont;
      font-size: 24px;
      line-height: 36px;
      color: #242020;
      opacity: 0.7; } }

  &__description-more {
    display: none;
    font-family: $robotoFont;
    font-weight: normal;
    font-size: 16px;
    line-height: 24px;
    color: rgba($colorPrimary, .7);
    cursor: pointer;
    margin-top: 15px;
    &:hover {
      color: $colorPrimary; } }

  &__rows {
    margin-top: 60px;
    .row + .row {
      margin-top: -20px; } }

  &__image {
    &--right {
      margin-left: -10px !important;
      clip-path: polygon(5.4% 0, 100% 0, 100% 100%, 0% 100%);
      .scaler {
        height: 510px;
        // img
 } }        // object-position: right center
    &--left {
      margin-right: -10px !important;
      clip-path: polygon(0 0, 100% 0%, 94.5% 100%, 0% 100%);
      .scaler {
        height: 540px; } } }

  &__text {
    max-width: 580px;
    padding-top: 61px;
    padding-bottom: 28px;
    &--left {}

    &--right {
      margin-top: auto;
      margin-bottom: auto;
      margin-left: 40px; } } }


//.career-jobs
.career-jobs {
  margin-top: 66px;
  padding-bottom: 239px;
  &__nav {
    position: relative;
    display: flex;
    align-items: center;
    margin: 88px 0 0;
    padding: 0 0 20px;
    overflow-x: auto;
    li {
      flex: 0 0 auto;
      display: inline-block;
      margin-right: 10px;
      &:last-child {
        margin-right: 0; } }
    a {
      position: relative;
      display: inline-block;
      padding: 5px;
      font-family: $latoFont;
      font-weight: bold;
      font-size: 14px;
      line-height: 17px;
      letter-spacing: 0.06em;
      text-transform: uppercase;
      padding: 5px 19px 5px 20px;
      border: 1px solid #3E3E3E;
      transition: color .2s $ease, background .2s $ease, border .2s $ease;
      &:hover {
        background: rgba(196, 196, 196, 0.2); } }
    li {
      &.is-active {
        a {
          color: $colorWhite;
          border-color: $colorPrimary;
          background: linear-gradient(180deg, #F5A02E 0%, #ED8900 100%); } } } }

  &__tab {
    margin-top: 31px; } }

// .career-job
.career-job {
  &__intro {
    margin-bottom: -12px;
    p {
      font-family: $robotoFont;
      font-size: 24px;
      line-height: 36px;
      color: #242020;
      opacity: 0.7; } }

  &__section {
    position: relative;
    padding: 53px 0 56px;
    & + & {
      &::after {
        display: inline-block;
        content: ' ';
        position: absolute;
        top: -2px;
        left: 0;
        right: 0;
        height: 1px;
        background: $colorPrimary; } }
    .col {
      &:first-child {
        .career-job__section-content {
          padding-right: 20px;
          padding-left: 1px; } }
      &:last-child {
        .career-job__section-content {
          padding-left: 21px; } } } }
  &__section-title {
    // h3
    margin-bottom: 28px; }

  &__contact {
    margin-top: 39px;
    padding-top: 14px;
    position: relative;
    &::before {
      display: inline-block;
      content: ' ';
      position: absolute;
      top: -1px;
      left: 0;
      width: 140px;
      height: 1px;
      background: $colorPrimary; }
    .p-regular {
      margin-top: 16px;
      color: rgba(#242020, 0.7); } }
  &__contact-title {
    font-family: $robotoFont;
    font-weight: bold;
    font-size: 22px;
    line-height: (28 / 22);
    letter-spacing: 0.04em;
    text-transform: uppercase;
    a {
      color: $colorPrimary;
      transition: color .25s $ease;
      text-decoration: underline;
      &:hover {
        color: $fontColor;
        text-decoration: none; } } } }

@include r($hdplus) {
  .career-intro {
    &__image {
      &--right {
        clip-path: polygon(7% 0, 100% 0, 100% 100%, 0% 100%); }
      &--left {
        clip-path: polygon(0 0, 100% 0%, 93% 100%, 0% 100%); } } } }

@include r($hd) {
  .career-intro {
    &__image {
      &--right {
        clip-path: polygon(8% 0, 100% 0, 100% 100%, 0% 100%); }
      &--left {
        clip-path: polygon(0 0, 100% 0%, 92% 100%, 0% 100%); } }
    &__text {
      max-width: 420px;
      &--right {
        margin-bottom: -5px;
        padding-bottom: 0; } } }

  .career-job {
    &__section {
      .col {
        &:first-child {
          .career-job__section-content {
            padding-right: 20px; } }
        &:last-child {
          .career-job__section-content {
            padding-left: 10px; } } } }
    &__contact {
      margin-top: 15px; } } }

@include r($xl) {
  .career-intro {
    padding-top: 146px;
    &__image {
      &--right {
        clip-path: polygon(10% 0, 100% 0, 100% 100%, 0% 100%); }
      &--left {
        clip-path: polygon(0 0, 100% 0%, 90% 100%, 0% 100%); } }
    &__text {
      max-width: 420px;
      padding-left: 2px; } } }

@include r($lg) {
  .career-intro, .career-jobs {
    .section-head__front {
      font-family: $baseFont;
      font-size: 64px;
      margin-top: 0;
      transform: translateY(6px); } }

  .career-intro {
    margin-bottom: 19px;
    &__rows {
      margin-top: 48px; }
    &__image {
      &--right {
        clip-path: polygon(15% 0, 100% 0, 100% 100%, 0% 100%);
        .scaler {
          height: 591px; } }
      &--left {
        clip-path: polygon(0 0, 100% 0%, 86% 100%, 0% 100%); } }
    &__text {
      max-width: 324px;
      &--right {
        margin-left: 8px;
        margin-top: 0;
        padding-top: 102px; } } }

  .career-jobs {
    margin-top: 19px;
    padding-bottom: 119px; }

  .career-job {
    &__section {
      padding-top: 0;
      padding-bottom: 20px;
      &:nth-child(2) {
        padding-top: 29px; }
      & + & {
        &::after {
          display: none; } }
      .col {
        &:first-child {
          .career-job__section-content {
            padding-right: 0px; } }
        &:last-child {
          .career-job__section-content {
            padding-left: 0px; } } } }
    &__section-content {
      margin-top: 28px; }

    &__contact {
      margin-top: 35px; } } }


@include r($md) {
  .career-intro, .career-jobs {
    .section-head__front {
      // margin-top: 5px
      font-family: $robotoFont;
      font-size: 36px;
      transform: translateY(-2px); } }

  .career-intro {
    padding-top: 63px;
    margin-bottom: 18px;
    &__rows {
      margin-top: 31px; }

    &__image {
      .scaler {
        height: auto;
        padding-bottom: 56.1%; }
      &--right {
        margin-left: 0 !important;
        margin-right: -20px;
        clip-path: polygon(5.4% 0, 100% 0, 100% 100%, 0% 100%); }
      &--left {
        margin-left: -20px;
        margin-right: 0 !important;
        clip-path: polygon(0 0, 100% 0%, 94.5% 100%, 0% 100%); } }
    &__description {
      margin-top: 25px;
      p {
        font-size: 16px;
        line-height: 24px; } }
    &__description-wrapper {
      height: 72px;
      overflow: hidden;
      transition: height .25s $ease; }
    &__description-more {
      display: inline-block; }

    &__text {
      max-width: 100%;
      padding-top: 16px;
      padding-left: 0;
      padding-bottom: 36px;
      margin-left: 0;
      &--right {
        padding-bottom: 0; } } }

  .career-jobs {
    margin-top: 8px;
    padding-bottom: 97px;
    &__nav {
      margin-top: 26px; }
    &__tab {
      margin-top: 4px; } }

  .career-job {
    &__intro {
      margin-bottom: -9px;
      p {
        font-size: 16px;
        line-height: 24px; } }

    &__contact {
      padding-top: 16px;
      max-width: 280px;
      .p-regular {
        margin-top: 21px; } }
    &__contact-title {
      font-size: 16px; }
    &__section-content {
      margin-right: -2px; } } }



@include r($sm) {
  .career-intro {
    padding-top: 105px;
    &__rows {
      margin-top: 24px; }
    &__description-wrapper {
      height: 139px; }
    &__text {
      padding-top: 20px; } }

  .career-jobs {
    margin-top: 43px;
    padding-bottom: 109px;
    .section-head {
      margin-right: 0; } }

  .career-job {
    &__intro {
      margin-bottom: 0px; }
    &__section-content {
      margin-top: 18px;
      margin-right: 2px; }
    &__contact {
      margin-top: 44px; } }

  .career-job__section .col:last-child .career-job__section-content {
    margin-top: 30px; }
  .career-job__section .col:last-child .career-job__section-content {} }
