// titles
h1, h2, h3, h4, h5, h6 {
  margin: 0; }

.h1-title, .h2-title, .h3-title, .h4-title, .h5-title, .h6-title {
  font-family: $robotoFont;
  font-weight: 700;
  text-transform: uppercase; }

.h1-title {
  font-size: 64px;
  line-height: (83 / 64); }

.h2-title {
  font-size: 36px;
  line-height: (42 / 36); }

.h3-title {
  font-size: 22px;
  line-height: (28 / 22);
  letter-spacing: 0.04em; }

.h4-title {
  font-size: 18px; }

.h5-title {
  font-size: 16px; }

.h6-title {
  font-size: 16px; }


