// paragraphs
.p-regular {
  font-family: $robotoFont;
  font-weight: normal;
  font-size: 16px;
  line-height: 1.5;
  color: $colorGray; }

.p-small {
  font-family: $robotoFont;
  font-weight: normal;
  font-size: 14px;
  line-height: (20 / 14);
  color: $colorGray; }

.p-regular, .p-small {
  a {
    color: $colorPrimary;
    transition: color .25s $ease;
    &:hover {
      color: $colorWhite; } } }

p {
  margin: 0;
  & + p {
    margin-top: 1em; } }

a {
  color: inherit;
  text-decoration: none;
  &:hover, &:focus {
    text-decoration: none; } }

ul {
  list-style: none;
  li {
    display: inline-block; } }

.ol-list {
  margin: 0;
  padding: 0;
  li {
    position: relative;
    display: block;
    font-family: $robotoFont;
    margin-bottom: 47px;
    &:last-child {
      margin-bottom: 0; } }
  &__count {
    position: absolute;
    left: 0;
    top: -7px;
    font-weight: bold;
    font-size: 36px;
    line-height: 42px;
    text-transform: uppercase;
    color: rgba($colorBackground, .25); }
  &__content {
    padding-left: 41px; }
  &__name {
    font-weight: bold;
    font-size: 22px;
    line-height: 28px;
    letter-spacing: 0.04em;
    text-transform: uppercase;
    color: $fontColor; }
  &__desc {
    margin-top: 8px;
    font-size: 16px;
    line-height: 24px;
    color: #242020;
    opacity: 0.7; } }

.ul-list {
  margin: 0;
  padding: 0;
  li {
    position: relative;
    display: block;
    font-family: Roboto;
    font-size: 16px;
    line-height: 24px;
    color: #242020;
    opacity: 0.7;
    margin-bottom: 16px;
    padding-left: 26px;
    &:last-child {
      margin-bottom: 0; }
    &::before {
      display: inline-block;
      content: ' ';
      position: absolute;
      left: 0;
      top: 9px;
      size: 6px;
      border-radius: 50%;
      background: $colorPrimary; } } }



