// FOOTER
.footer {
  position: relative;
  z-index: 2;
  padding-top: 6px;
  padding-bottom: 115px;
  background: $fontColor;
  color: $colorWhite;
  overflow: hidden;
  &__background {}

  &__content {
    position: relative;
    z-index: 2; }

  &__form {
    margin-top: 84px;
    textarea {
      min-height: 141px; } }
  &__form-inputs {
    padding-right: 20px; }
  &__form-cta {
    position: relative;
    margin-top: 59px;
    display: flex;
    justify-content: center;
    &::before, &::after {
      display: inline-block;
      content: ' ';
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
      height: 1px;
      width: calc(50% - ((200px + 120px) / 2)); }
    &::before {
      left: 0;
      background: linear-gradient(to right, rgba($colorPrimary, 0) 0%, $colorPrimary 100%); }
    &::after {
      right: 0;
      background: linear-gradient(to left, rgba($colorPrimary, 0) 0%, $colorPrimary 100%); } }

  &__bottom {
    margin-top: 115px;
    display: flex; }
  &__copy {
    opacity: 0.7;
    font-family: $robotoFont;
    font-size: 14px;
    line-height: 20px;
    padding-right: 20px;
    .br-xl, .br-sm {
      display: none; } }
  &__socials {
    font-family: $robotoFont;
    letter-spacing: 0;
    font-size: 14px;
    line-height: 20px;
    margin-left: auto;
    padding-right: 2px;
    a {
      position: relative;
      display: inline-block;
      margin-right: 21px;
      transition: color .25s $ease;
      &::after {
        display: inline-block;
        content: '/';
        position: absolute;
        right: -12.5px;
        color: $colorWhite; }
      &:last-child {
        margin-right: 0;
        &::after {
          display: none; } }
      &:hover {
        color: $colorPrimary; } } } }

@include r($hd) {
  .footer {
    &__form-inputs {
      padding-right: 0; }
    &__form-textarea {
      padding-left: 20px; }
    &__copy {
      padding-right: 0; }
    &__socials {
      a {
        margin-right: 17px;
        &::after {
          right: -11.5px; } } } } }

@include r($xl) {
  .footer {
    &__copy {
      padding-left: 2px;
      br, .br-sm {
        display: none; }
      .br-xl {
        display: block; } }

    &__socials {
      a {
        margin-right: 17.5px;
        &::after {
          right: -11.5px; } } } } }

@include r($lg) {
  .footer {
    padding-bottom: 75px;
    &__form {
      margin-top: 82px; }
    &__form-inputs {
      display: flex;
      .ui-group {
        width: 100%;
        flex: 0 0 50%;
        max-width: 50%;
        min-width: 1px;
        min-height: 0;
        margin-bottom: 5px;
        &:first-child {
          padding-right: 20px; }
        &:last-child {
          padding-left: 20px; } } }

    &__form-textarea {
      padding-left: 0; }
    &__form-cta {
      margin-top: 58px; }
    &__bottom {
      flex-direction: column;
      align-items: center; }
    &__copy {
      order: 2;
      margin-top: 11px;
      text-align: center;
      padding-right: 0;
      padding-left: 0px;
      br {
        display: block; }
      .br-xl, .br-sm {
        display: none; } }
    &__socials {
      order: 1;
      margin-left: 0; } } }

@include r($md) {
  .footer {
    padding-bottom: 100px;
    &__head {
      transform: translateY(3px);
      .section-head__back {
        transform: translate(-21px, 5px); } }
    &__form {
      margin-top: 14px; }
    &__form-inputs {
      display: flex;
      display: block;
      .ui-group {
        max-width: 100%;
        margin-bottom: 25px;
        &:first-child {
          padding-right: 0; }
        &:last-child {
          padding-left: 0;
          margin-bottom: 5px; } } }
    &__form-cta {
      justify-content: flex-start;
      &::before, &::after {
        display: none; } }

    &__bottom {
      margin-top: 54px;
      align-items: flex-start; }
    &__copy {
      margin-top: 30px;
      text-align: left; }
    &__socials {
      a {
        margin-right: 16.5px;
        &::after {
          right: -13px; } } } } }
@include r($sm) {
  .footer {
    padding-top: 52px;
    &__head {
      transform: translateY(4px);
      .section-head__back {
        transform: translate(-21px, 2px); } }
    &__form {
      margin-top: 21px; }
    &__bottom {
      margin-top: 50px; }
    &__copy {
      width: 100%;
      margin-top: 26px;
      text-align: center;
      transform: translateX(2px);
      .br-sm {
        display: block; } }
    &__socials {
      text-align: center;
      line-height: 30px;
      max-width: 280px;
      a {
        &:nth-child(3) {
          margin-right: 0;
          &::after {
            display: none; } }
        &:nth-child(4) {
          margin-right: 19px;
          margin-left: 2px; }
        &:nth-child(5) {
          margin-left: 2px; } } }

    .section-head {
      &__front {
        transform: translate(-1px,-6px); } } } }
