// bgimg
.bgimg {
  position: absolute 0;
  min-width: 1px;
  min-height: 0;
  display: flex;
  flex-direction: column;
  img, picture {
    max-width: 100%;
    max-height: 100%;
    flex-grow: 1;
    object-fit: cover;
    object-position: center center; } }

.mx-auto {
  margin-left: auto;
  margin-right: auto; }

// desktop first utils
@include r($hd) {
  .hd-hidden {
    display: none !important; } }

@include r($xl) {
  .xl-hidden {
    display: none !important; } }

@include r($lg) {
  .lg-hidden {
    display: none !important; } }

@include r($md) {
  .md-hidden {
    display: none !important; } }

@include r($sm) {
  .sm-hidden {
    display: none !important; } }

// mobile first utils
@include rmin($lg + 1) {
  .lg-visible {
    display: none !important; } }

@include rmin($md + 1) {
  .md-visible {
    display: none !important; } }

@include rmin($sm + 1) {
  .sm-visible {
    display: none !important; } }
