// PORTFOLIO
.portfolio {
  position: relative;
  z-index: 2;
  padding-top: 139px;
  padding-bottom: 120px;
  background: $fontColor;
  color: $colorWhite; }


@include r($md) {
  .portfolio {
    padding-top: 69px;
    padding-bottom: 62px; } }

@include r($sm) {
  .portfolio {
    padding-bottom: 62px; } }
