* {
  box-sizing: border-box;
  outline: none; }

// FOUC like fix (prevent transition from default browser values)
body:not(.is-ready) {
  * {
    transition: none !important; } }

body, html {
  background: $colorWhite;
  color: $fontColor;
  font-family: $baseFont;
  // font-family: "Helvetica", -apple-system, 'BlinkMacSystemFont', 'Segoe UI', 'Roboto', 'Helvetica', 'Ubuntu', 'Cantarell', 'Arial', sans-serif, 'Apple Color Emoji', 'Segoe UI Emoji', 'Segoe UI Symbol'
  font-size: 16px;
  min-width: 320px;
  max-width: 100%;
  margin: 0;
  overflow-x: hidden;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

@include scrollbar {
  height: 16px;
  overflow: visible;
  width: 16px;
  display: none; }

::selection {
  color: $colorWhite;
  background: $colorPrimary; }

.is-ios {
  cursor: pointer;
  * {
    cursor: pointer; } }

.body-lock {
  height: 100%;
  overflow: hidden;
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  width: 100%;
  &.is-mobile {
    overflow: visible; } }

.page {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  &.dark {
    background: $fontColor; }
  &__content {
    // used for sticky footer (flexbox way)
    flex: 1 0 auto; } }
