/* UI */
.ui-group {
  position: relative;
  margin-bottom: 25px;
  &:last-child {
    margin-bottom: 0; }
  &__label {
    display: block;
    margin-bottom: 12px;
    font-family: $robotoFont;
    font-weight: bold;
    font-size: 18px;
    line-height: 128.91%;
    letter-spacing: 0.04em;
    text-transform: uppercase; }
  .btn, .ui-upload {
    // mind the label space
    margin-top: calc(16px + 12px); }
  .ui-upload {
    .btn {
      margin-top: 0; } } }

@include r($md) {
  .ui-group {
    margin-bottom: 15px; }
  .col-12, .col-sm-12, .col-md-12, .col-lg-12 {
    .ui-group {
      .btn, .ui-upload {
        margin-top: 0px; } } } }
