/* Hamburger */
/* https://codepen.io/ahmadbassamemran/pen/abopOMY */
.hamburger {
  position: relative;
  width: 28px;
  height: 24px;
  padding: 0px;
  background-color: transparent;
  border-style: none;
  outline: none;
  cursor: pointer;
  transform: scale(0.75) translateY(0px);
  transition: all .8s linear;
}

.hamburger span {
  width: 100%;
  height: 2px;
  background-color: #F38C00;
  display: block;
  position: absolute;
  border-radius: 4px;
  transition: all .5s ease;
  will-change: transform;
}

.hamburger span:nth-child(1) {
  top: 0px;
  left: 0px;
}

.hamburger span:nth-child(2) {
  top: 9px;
  right: 0px;
}

.hamburger span:nth-child(3) {
  top: 18px;
  left: 0px;
}

.hamburger span:nth-child(1) {
  animation: span1_close 0.8s forwards cubic-bezier(0.8, 0.5, 0.2, 1.4);
}

.hamburger span:nth-child(2) {
  animation: span2_close 0.8s forwards cubic-bezier(0.8, 0.5, 0.2, 1.4);
}

.hamburger span:nth-child(3) {
  animation: span3_close 0.8s forwards cubic-bezier(0.8, 0.5, 0.2, 1.4);
}

.is-active .hamburger {
  transform: translate3d(0px, -3px, 0) scale(0.75);
  transition: all 1.1s .8s linear;
}

.is-active .hamburger span:nth-child(2) {
  animation: span2 1.5s forwards cubic-bezier(0.8, 0.5, 0.2, 1.4);
}

.is-active .hamburger span:nth-child(3) {
  animation: span3 0.8s forwards cubic-bezier(0.8, 0.5, 0.2, 1.4);
  animation-delay: .7s;
}

.is-active .hamburger span:nth-child(1) {
  animation: span1 0.8s forwards cubic-bezier(0.8, 0.5, 0.2, 1.4);
  animation-delay: 1.1s;
}

@keyframes span2 {
  0% {
    transform: translate(0, 0) scale(1);
  }
  20%, 40%, 50%, 80%, 100% {
    width: 4px;
    height: 4px;
    border-radius: 50%;
  }
  20% {
    transform: translate(0, 0) scale(1);
  }
  40% {
    transform: translate(0, 0) scale(1);
  }
  50% {
    transform: translate(0, 9px) scale(1);
  }
  80% {
    transform: translate(0, -9px) scale(1);
  }
  100% {
    transform: translate(0, 30px) scale(0);
  }
}

@keyframes span3 {
  0% {
    transform: translate(0, 0);
  }
  60% {
    transform: rotate(80deg) translate(-5px, -4px);
  }
  100% {
    transform: rotate(45deg) translate(-5px, -4px);
  }
}

@keyframes span1 {
  0% {
    transform: translate(0, 0);
  }
  60% {
    transform: rotate(-110deg) translate(-5px, -4px);
  }
  100% {
    transform: rotate(-45deg) translate(-9px, 9px);
  }
}

@keyframes span1_close {
  0% {
    transform: rotate(-45deg) translate(-9px, 9px);
  }
  100% {
    transform: rotate(0deg) translate(0, 0);
  }
}

@keyframes span2_close {
  0% {
    transform: translate(0, 18px) scale(0);
  }
  100% {
    transform: translate(0, 0) scale(1);
  }
}

@keyframes span3_close {
  0% {
    transform: rotate(45deg) translate(-5px, -4px);
  }
  100% {
    transform: rotate(0deg) translate(0, 0);
  }
}

