  color: #FBFFFE;
  will-change: transform;
  backface-visibility: hidden;
  transform-style: preserve-3d;
}

.header__wrapper {
  display: flex;
  align-items: center;
  padding: 19px 60px 18px;
}

.header__logo {
  flex: 0 0 auto;
  padding-right: 15px;
  font-size: 0;
}

