}

.header.is-fixed {
  position: fixed;
  transform: translate3d(0, -100%, 0) !important;
  transition: transform .25s ease;
}

.header.is-fixed .header__wrapper {
  padding-top: 10px;
  padding-bottom: 10px;
}

.header.is-fixed-visible {
  transform: translate3d(0, 0, 0) !important;
}

.header.is-transitioning {
  transition: transform .25s ease;
  transform: translate3d(0, -100%, 0) !important;
}

