// LAYOUT
$mobile-s: 375;
$sm: 639;
$md: 767;
$lg: 1023;
$xl: 1199;
$hd: 1509;
$hdplus: 1919;

// FONTS
$baseFont: "PT Sans", sans-serif;
$latoFont: 'Lato', sans-serif;
$robotoFont: 'Roboto', sans-serif;

$systemFont: -apple-system, 'BlinkMacSystemFont', 'Segoe UI', 'Roboto', 'Helvetica', 'Ubuntu', 'Cantarell', 'Arial', sans-serif, 'Apple Color Emoji', 'Segoe UI Emoji', 'Segoe UI Symbol';

// OPTIONS
$transitionSpeed: .25s;
$ease: ease-in-out;

// COLORS
// Color-names https://htmlcolorcodes.com/color-names/
$fontColor: #0F0F0F;
$colorWhite: #FBFFFE;
$colorGray: #C4C4C4;
$colorPrimary: #F38C00;
$colorAccent: teal;
$colorGreen: mediumaquamarine;
$colorRed: #D33F49;
$colorRedHover: lighten($colorRed, 15%);
$borderColor: #e8e8e8;
$colorBg: gainsboro;
$colorBackground: #5C5D5D;


// SOCIAL colors
$twitter: #00aced;
$facebook: #3b5998;
$googleplus: #dd4b39;
$pinterest: #cb2027;
$linkedin: #007bb6;
$youtube: #bb0000;
$vimeo: #1ab7ea;
$tumblr: #32506d;
$instagram: #bc2a8d;
$flickr: #ff0084;
$dribbble: #ea4c89;
$quora: #a82400;
$foursquare: #0072b1;
$forrst: #5B9A68;
$vk: #45668e;
$wordpress: #21759b;
$stumbleupon: #EB4823;
$yahoo: #7B0099;
$blogger: #fb8f3d;
$soundcloud: #ff3a00;

// BootStrap 12-col GRID
$gutter: 10px;
$col1: percentage(1 / 16);
