
// PORTFOLIO LIST
.portfolio-list {
  padding: 0 60px 60px;
  &__nav {
    position: relative;
    display: flex;
    align-items: center;
    margin: 0px 0 0;
    padding: 0 0 20px;
    overflow-x: auto;
    li {
      flex: 0 0 auto;
      display: inline-block;
      margin-right: 10px;
      &:last-child {
        margin-right: 0; } }
    a {
      position: relative;
      display: inline-block;
      padding: 5px;
      font-family: $latoFont;
      font-weight: bold;
      font-size: 14px;
      line-height: 17px;
      letter-spacing: 0.06em;
      text-transform: uppercase;
      padding: 6px 19px;
      border: 1px solid #3E3E3E;
      transition: color .2s $ease, background .2s $ease, border .2s $ease;
      &:hover {
        background: rgba(196, 196, 196, 0.2); } }
    li {
      &.is-active {
        a {
          color: $fontColor;
          border-color: $colorPrimary;
          background: linear-gradient(180deg, #F5A02E 0%, #ED8900 100%); } } } }

  &__masonry {
    margin: 29px -10px -10px;
    min-height: 100vw;
    opacity: 0;
    &.is-loaded {
      opacity: 1;
      min-height: 1px; }
    .grid-sizer {
      width: percentage(1 / 4); } }

  &__more {
    margin-top: 30px;
    display: flex;
    justify-content: flex-end; } }


// .p-category
.p-category {
  width: percentage(1 / 4);
  font-size: 0;
  padding: 10px;
  cursor: pointer;
  &--wide {
    width: percentage(2 / 4); }
  &__wrapper {
    position: relative;
    z-index: 1;
    overflow: hidden;
    box-shadow: 0px 6px 12px rgba(0, 0, 0, 0.6); }
  &__image {
    position: relative;
    overflow: hidden;
    cursor: pointer;
    font-size: 0;
    &.is-dynamic {
      opacity: 0;
      will-change: opacity;
      transition: opacity .1s $ease;
      &.is-loaded {
        opacity: 1; } }
    img, picture {
      width: 100%;
      // // TODO - bug exporting image
 }      // transform: scale(1.02)
    &::after {
      display: inline-block;
      content: ' ';
      position: absolute 0;
      z-index: 1;
      background: linear-gradient(180deg, rgba(0, 0, 0, 0) 36.68%, rgba(0, 0, 0, 0.8) 100%); } }
  &__info {
    position: absolute 0;
    z-index: 2;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    padding: 13px 20px;
    background: rgba($fontColor, .6);
    opacity: 0;
    pointer-events: none;
    will-change: opacity;
    transition: opacity .1s $ease; }
  &__name {
    font-size: 36px;
    line-height: (42 / 36);
    text-transform: uppercase; }
  &__categories {
    margin-top: 19px;
    font-family: $robotoFont;
    font-weight: bold;
    font-size: 16px;
    line-height: (24 / 16);
    color: rgba($colorGray, .7); }
  &__name, &__categories {
    will-change: transform;
    transform: translate3d(0,100%,0);
    transition: transform .25s $ease; }
  &:hover {
    .p-category {
      &__info {
        opacity: 1;
        pointer-events: all; }
      &__name, &__categories {
        transform: translate3d(0,0,0); } } } }


@include r($hd) {
  .p-category {
    &__info {
      padding-right: 10px; }
    &__name {
      font-family: $robotoFont;
      font-size: 22px;
      letter-spacing: 0.04em; }
    &__categories {
      font-size: 14px; } }

  .portfolio-list {
    &__more {
      margin-top: 24px;
      .swiper-nav-arrows__label {
        transform: translateY(-2px); } } } }

@include r($xl) {
  .portfolio-list {
    padding: 0 40px 27px; } }

@include r($lg) {
  .portfolio-list {
    padding: 0 40px 32px;
    &__nav {
      margin-left: -40px;
      margin-right: -40px;
      padding-left: 40px;
      padding-right: 40px;
      &::after {
        display: inline-block;
        content: ' ';
        width: 40px;
        height: 2px;
        flex: 0 0 40px; } } } }

@include r($md) {
  .portfolio-list {
    padding-left: 20px;
    padding-right: 20px;
    padding-bottom: 0;
    &__nav {
      margin-left: -20px;
      margin-right: -20px;
      padding-left: 20px;
      padding-right: 20px;
      &::after {
        width: 20px;
        flex: 0 0 20px; } } } }
