
// tooltip
.tippy-box[data-theme~=light] {
  color: $fontColor;
  background: rgba(#FBFFFE, 0.9);
  box-shadow: 0px 6px 12px rgba(0, 0, 0, 0.6);
  border-radius: 0; }

.tippy-content {
  padding: 2px 5px; }


.tooltip {
  font-family: $robotoFont;
  color: $fontColor;
  &__title {
    font-weight: bold;
    font-size: 12px;
    line-height: 152.34%;
    text-transform: uppercase; }
  &__description {
    font-weight: normal;
    font-size: 10px;
    line-height: 140.62%; } }
