.container {
  // 1260 + 120
  max-width: 1380px;
  width: 100%;
  margin-left: auto;
  margin-right: auto;
  padding-left: 60px;
  padding-right: 60px;
  &--1620 {
    max-width: 1740px;
    padding-left: 60px;
    padding-right: 60px; } }

@include r($hdplus) {
  .container {
    &--1620 {
      max-width: 1710px;
      padding-left: 45px;
      padding-right: 45px; } } }

@include r($hd) {
  .container {
    padding-left: 130px;
    padding-right: 130px;
    &--r60 {
      padding-right: 60px; }
    &--1620 {
      padding-left: 10px;
      padding-right: 10px; } } }

@include r($xl) {
  .container {
    padding-left: 40px;
    padding-right: 40px; } }


@include r($md) {
  .container {
    padding-left: 20px;
    padding-right: 20px; } }


.section {
  position: relative; }

@include r($xl) {
  .section[data-hash="posters"] {
    visibility: hidden;
    opacity: 0; } }

.row {
  display: flex;
  flex-wrap: wrap;
  margin: -$gutter; }

.col {
  display: flex;
  flex-direction: column;
  min-width: 1px;
  min-height: 0;
  padding: $gutter; }

