// hero
.hero {
  &__slide {
    position: relative;
    z-index: 1;
    overflow: hidden; }
  &__slide-background {
    font-size: 0;
    .scaler {
      padding-bottom: 40%; } }

  &__slide-content {
    position: absolute;
    z-index: 2;
    padding: 120px;
    left: 0;
    top: 50%;
    transform: translateY(-50%);
    color: $colorWhite;
    text-align: center; }
  &__slide-logo {
    img, picture {
      max-width: 100%; } }
  &__slide-date {
    font-family: $latoFont;
    margin-top: 14px;
    font-weight: bold;
    font-size: 35px;
    line-height: (42 / 35); }
  &__slide-platforms {
    margin-top: 35px;
    display: flex;
    align-items: center;
    justify-content: space-between; }

  .swiper-pagination {
    position: absolute;
    z-index: 2;
    bottom: 20px;
    left: 0;
    right: 0; } }

@include r($hd) {
  .hero {
    &__slide-content {
      padding-left: 130px; } } }

@include r($xl) {
  .hero {

    &__slide-content {
      padding: 87px 40px;
      top: 0;
      transform: none; }
    &__slide-date {
      margin-top: 16px;
      font-size: 30px; }
    &__slide-content-wrapper {
      transform: scale(0.73);
      transform-origin: left top; }
    &__slide-platforms {
      margin-top: 42px; } } }

@include r($lg) {
  .hero {
    &__slide-content {
      padding: 96px 40px 20px; }
    &__slide-content-wrapper {
      transform: scale(0.47); }
    &__slide-date {
      margin-top: 16px;
      font-size: 34px; }
    &__slide-platforms {
      margin-top: 36px; }
    .swiper-nav-full {
      display: none; } } }

@include r($md) {
  .hero {
    margin-top: 50px;
    &__slide-background {
      position: relative;
      &::after {
        display: inline-block;
        content: ' ';
        position: absolute 0;
        z-index: 2;
        background: linear-gradient(180deg, rgba(15, 15, 15, 0) 60.55%, rgba(15, 15, 15, 0.8) 100%); }
      .scaler {
        padding-bottom: 48.4%; } }
    &__slide-content {
      padding: 72px 28px 20px;
      text-align: left; }
    &__slide-content-wrapper {
      transform: none; }
    &__slide-logo {
      max-width: 225px; }
    &__slide-date {
      margin-top: 12px;
      font-size: 18px; }
    &__slide-platforms {
      margin-top: 16px;
      .icon {
        transform: scale(0.74);
        transform-origin: left center;
        + .icon {
          margin-left: -9px; } }
      .icon-xbox-one {
        margin-left: -12px !important; }
      .icon-ps-4 {
        margin-left: -12px !important; }
      .icon-pc {
        margin-left: -4px !important; } }


    .swiper-pagination {
      display: none; } } }

@include r($sm) {
  .hero {
    &__slide-background {
      .scaler {
        padding-bottom: 0;
        height: 230px; } }
    &__slide-content {
      padding: 52px 20px 20px; }
    &__slide-logo {
      max-width: 169px;
      transform: translateY(1px); }
    &__slide-date {
      margin-top: 10px; }
    &__slide-platforms {
      margin-top: 4px;
      .icon {
        transform: scale(0.57); }
      .icon-xbox-one {
        margin-left: -34px !important; }
      .icon-ps-4 {
        margin-left: -40px !important; }
      .icon-pc {
        margin-left: -24px !important; } } } }
