// Team
.team {
  position: relative;
  z-index: 2;
  padding-top: 6px;
  padding-bottom: 79px;
  // background: $fontColor
  color: $colorWhite;
  overflow: visible;
  &__content {
    position: relative;
    z-index: 2; }
  &__description {
    margin-top: 83px;
    max-width: 1100px; }

  &__slider {
    margin-top: 42px; }

  &__thumbs {
    position: relative;
    .swiper-container {
      height: 425px;
      width: 140px;
      margin-left: 0; }
    &::after {
      display: inline-block;
      content: ' ';
      position: absolute;
      right: 20px;
      top: 50%;
      transform: translateY(-50%);
      width: 1px;
      height: 248px;
      background: $colorPrimary; }
    .swiper-slide {
      cursor: pointer;
      opacity: 1;
      width: 60px;
      backface-visibility: hidden;
      transform: translate3d(0,0,0);
      transition: opacity .25s $ease;
      &.shift-top {
        margin-top: 45px; }
      &.is-muted {
        opacity: 0.4; }
      &.is-hidden {
        opacity: 0;
        pointer-events: none; }
      &.swiper-slide-active {
 }        // opacity: 1

      &.is-active {
        opacity: 1;
        .hexagon {
          &__wrapper {
            background: $colorPrimary; }
          &__image {
            // margin: 1px
            &::after {
              opacity: 1; } } } }
      &:hover {
        opacity: 1;
        .hexagon {
          &__image {
            &::after {
              opacity: 1; } } } } }

    .hexagon {
      // &::after
      //   display: inline-block
      //   content: ' '
      //   position: absolute 0
      //   z-index: 21
      //   background: linear-gradient(180deg, rgba(15, 15, 15, 0) 0%, rgba(15, 15, 15, 0.4) 100%)
      &__wrapper {
        width: 60px;
        height: 68px;
        background: $fontColor;
        transition: background .25s $ease; }
      &__image {
        margin: 1px;
        img {
          filter: grayscale(100%); }
        &::after {
          display: inline-block;
          content: ' ';
          position: absolute 0;
          z-index: 2;
          background: rgba($colorPrimary, 0.2);
          opacity: 0;
          backface-visibility: hidden;
          transition: opacity .25s $ease; } } } }

  &__main {
    position: relative;
    flex: 1 1 auto;
    display: flex;
    flex-direction: column;
    .swiper-nav-arrows {
      position: absolute;
      left: 198px;
      bottom: 60px;
      z-index: 3; } } }


// team card
.team-card {
  position: absolute;
  top: 50%;
  left: 0;
  transform: translate3d(0,-50%,0);
  display: flex;
  align-items: center;
  padding-left: 21px;
  pointer-events: none;
  will-change: transform;
  backface-visibility: hidden;
  .hexagon {
    opacity: 0;
    transition: opacity .1s $ease;
    &__wrapper {
      width: 266px;
      height: 304px; } }

  &__content {
    padding-left: 60px;
    max-width: 600px;
    padding-top: 10px;
    padding-bottom: 18px;
    will-change: transform;
    opacity: 0;
    transform: translate3d(0,100px,0);
    transition: transform .25s $ease, opacity .1s $ease; }

  &__name {
 }    // h3-title instance

  &__position {
    position: relative;
    font-family: $robotoFont;
    font-size: 14px;
    line-height: (20 / 14);
    padding-bottom: 16px;
    margin-top: 14px;
    &::after {
      display: inline-block;
      content: ' ';
      position: absolute;
      bottom: 0;
      left: 0;
      height: 1px;
      width: 140px;
      background: $colorPrimary; } }
  &__description {
    margin-top: 34px; }
  &__description-more {
    display: none;
    font-family: $robotoFont;
    font-weight: normal;
    font-size: 16px;
    line-height: 24px;
    color: rgba($colorPrimary, .7);
    margin-top: 13px;
    cursor: pointer;
    &:hover {
      color: $colorPrimary; } }

  &.is-active {
    pointer-events: all;
    .hexagon {
      opacity: 1; }
    .team-card {
      &__content {
        opacity: 1;
        transform: translate3d(0,0,0); } } }
  &:hover {
    .hexagon {
      opacity: 1; } } }

@include r($hdplus) {
  .team-card {
    &__content {
 } } }      // padding-top: 34px

@include r($hd) {
  .team-card {
    align-items: flex-end;
    &__content {
      margin-top: auto;
      padding-top: 0;
      padding-bottom: 0;
      margin-bottom: -20px; } } }

@include r($lg) {
  .team {
    &__slider {
      margin-top: 16px; }
    &__thumbs {
      margin-top: 3px; }
    &__main {
      .swiper-nav-arrows {
        left: 112px;
        bottom: 97px; } } }

  .team-card {
    padding-left: 0;
    align-items: center;
    .hexagon {
      &__wrapper {
        width: 200px;
        height: 228px; } }
    &__content {
      padding-left: 40px;
      margin-bottom: 0;
      margin-right: -1px; } } }

@include r($md) {
  .team {
    padding-bottom: 0;
    &__description {
      margin-top: 22px; }

    &__thumbs {
      margin-top: -4px;
      position: relative;
      .swiper-container {
        height: 260px;
        width: auto;
        margin-left: 0; }
      &::after {
        display: none; }
      .swiper-slide {
        opacity: 1;
        padding-top: 20px;
        padding-bottom: 20px;
        height: calc(220px + 40px);
        width: calc((100% / 5) - 10px);
        transform: scale(0.85);
        &.shift-top {
          margin-top: 0px; }
        &.is-hidden {
          opacity: 1;
          pointer-events: all; }
        &.is-muted {
          opacity: 1; }
        &.is-hidden-mobile {
          opacity: 0;
          pointer-events: none; }
        &.swiper-slide-active {
          opacity: 1; }

        &.swiper-slide-active {
          z-index: 5;
          transform: scale(1);
          + .swiper-slide {
            z-index: 4;
            + .swiper-slide {
              z-index: 3;
              + .swiper-slide {
                z-index: 2; } } } }

        &.swiper-slide-active {
          .hexagon {
            &__wrapper {
              background: $colorPrimary; }
            &__image {
              // margin: 1px
              &::after {
                opacity: 1; } } } } }
      .hexagon {
        position: absolute;
        left: 50%;
        top: 20px;
        transform: translateX(-50%);
        filter: drop-shadow(0px 6px 12px rgba(black, 0.6));
        &__wrapper {
          width: 192px;
          height: 220px; } } }

    &__main {
      margin-top: -20px;
      .swiper-nav-arrows {
        display: none; } } }

  .team-card {
    top: 0;
    transform: none;
    &__image {
      display: none; }
    &__content {
      padding-left: 0;
      margin-top: -4px; }
    &__description {
      margin-top: 18px; }

    &__description-wrapper {
      height: 52px;
      overflow: hidden;
      transition: height .25s $ease;
      &.is-visible {
        // height: auto
 } }        // height to be set with js
    &__description-more {
      display: inline-block; } } }

@include r($sm) {
  .team {
    padding-top: 50px;
    &__slider {
      margin-top: 16px; }
    &__thumbs {
      margin-left: -20px;
      margin-right: -20px;
      .swiper-slide {
        width: calc((100% / 3) - 22px);
        transform: scale(0.83); } }
    &__main {
      margin-top: -20px; }
    &__description {
      p {
        line-height: 1.45; } } }
  .team-card {
    &__content {
      margin-top: 0; }
    &__description {
      margin-right: -4px; }
    &__description-wrapper {
      height: 76px; }
    &__position {
      margin-top: 8px;
      padding-bottom: 22px;
      &::after {
        width: 133px; } } } }
