/* Radio */
.ui-radio {
  input {
    display: none;
    width: 0.1px;
    height: 0.1px;
    opacity: 0;
    position: absolute;
    top: 0;
    left: 0;
    &:checked {
      + label {
        &::after {
          transform: translateY(-50%) scale(1); }
        span {
          color: $colorPrimary; } } } }
  label {
    display: block;
    width: 100%;
    position: relative;
    cursor: pointer;
    &::before {
      position: absolute;
      left: 0;
      top: 50%;
      transform: translateY(-50%);
      display: inline-block;
      content: ' ';
      size: 13px;
      background-color: transparent;
      border: 1px solid #3E3E3E;
      border-radius: 50%;
      cursor: pointer;
      vertical-align: middle;
      cursor: pointer;
      transition: all $transitionSpeed ease-in-out; }
    &::after {
      position: absolute;
      left: 3px;
      top: 50%;
      transform: translateY(-50%) scale(0);
      display: inline-block;
      content: ' ';
      size: 9px;
      background: $colorPrimary;
      border-radius: 50%;
      transition: transform .2s ease; }
    span {
      display: inline-block;
      padding-left: 25px;
      vertical-align: top;
      font-family: $robotoFont;
      font-weight: bold;
      font-size: 14px;
      line-height: 16px;
      letter-spacing: 0.04em;
      text-transform: uppercase;
      color: #3E3E3E;
      cursor: pointer;
      transition: color .25s $ease; }
    &:hover {
      span {
        color: $colorWhite; } } } }
